import FootNav from '@/components/foot-nav'
import Scroll from '@/components/scroll'
import Solution from './components/solution'
import ClassificationNav from './components/classificationNav'
export default {
  components: {
    FootNav,
    Scroll,
    Solution,
    ClassificationNav
  },
  data() {
    return {
      nowIndex: 0,
      tabsParam: ['解决方案', '分类导航']
    }
  },
  methods: {
    toggleTabs: function(index) {
      this.nowIndex = index
    }
  }
}



