import { loadData } from '@/api/common'
import Scroll from '@/components/scroll'
export default {
  data() {
    return {
      parentTitle: []
    }
  },
  components: {
    Scroll
  },
  created() {
    this.initDate()
  },
  methods: {
    initDate() {
      loadData('/article/menu_list', {
        type: 2
      }).then(data => {
        console.log(data)
        this.parentTitle = data.data
      })
    },
    toArticleDetails(id) {
      this.$router.push(`/healthCenter/components/articleDetails/${id}`)
    }
  }
}



